.container {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  width: 400px;
  margin: 100px auto 50px auto;
  /* padding: 15px; */
  background-color: rgba(255, 255, 255, 0.616);
}

.avatar {
  border-radius: 100%;
  margin: 0px 90px;
  width: 200px;
}

.description {
  align-items: center;
  flex-direction: column;
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  padding-top: 20px;
  background: linear-gradient(45deg, #cf9ab8 26%, #56b5b8 100%, #d0f2ed 0);
}

.description_name {
  margin: 0px;
  padding-top: 40px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 25px;
  font-weight: bold;
  color: rgba(7, 7, 7);
}

.description_tag {
  margin: 0px;
  padding: 15px 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
}

.description_location {
  margin: 0px;
  padding-bottom: 40px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
}

.stats {
  padding: 0px;
  margin: 0px;
  list-style: none;
  display: flex;
  justify-content: center;
  justify-content: space-around;
}

.contain_block {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 33.3%;
  height: 50px;
  padding: 50px 0px;
  border-right: 1px solid rgba(0, 0, 0, 0.15);
  background-color: rgb(93 180 184);
}

.contain_block:last-of-type {
  border-right: none;
}

.stats_label {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
  display: block;
  padding: 5px 0px;
}

.stats_quantity {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: rgb(0, 0, 0);
  padding: 5px 0px;
  display: block;
}
