.item {
  display: flex;
  align-items: center;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 15px;
  background: linear-gradient(45deg, #cf9ab8 26%, #56b5b8 100%, #d0f2ed 0);
}

.status {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
}

.online {
  composes: status;
  background-color: #00ff08;
}

.offline {
  composes: status;
  background-color: #ff1500;
}

.avatar {
  width: 60px;
  margin: 0px 20px 0px 10px;
  border-radius: 3px;
}

.name {
  margin: 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 30px;
  font-weight: 500;
  color: rgba(0, 0, 0);
}
