.transaction_history {
  width: 800px;
  margin: 0px auto;
  border-radius: 3px;
  text-align: center;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.2);
}

.title {
  padding: 15px;
  background: linear-gradient(45deg, #cf9ab8 26%, #56b5b8 100%, #d0f2ed 0);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 20px;
  font-weight: 500;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  width: 33.3%;
}
.title:last-child {
  border-top-right-radius: 3px;
}
.title:first-child {
  border-top-left-radius: 3px;
}

.titleItem {
  padding: 10px;
}
.titleItem:nth-child(2n) {
  background-color: #5e5e5e1a;
}

.titleItem_text {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.527);
  padding: 10px;
}
