.statistics {
  width: 400px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  margin: 0px auto;
  border-radius: 5px;
  background: linear-gradient(45deg, #cf9ab8 26%, #56b5b8 100%, #d0f2ed 0);
}

.title {
  display: flex;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 25px;
  font-weight: bold;
  color: rgba(61, 61, 61, 0.658);
  padding: 20px 0;
  text-transform: uppercase;
}

.statList {
  padding: 0px;
  list-style: none;
  display: flex;
  justify-content: space-around;
}

.statList_item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.statList_item:last-child {
  border-bottom-right-radius: 5px;
}

.statList_item:first-child {
  border-bottom-left-radius: 5px;
}

.statList_label {
  padding: 10px 0px 5px 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: white;
}

.statList_percentage {
  padding: 5px 0px 15px 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 25px;
  font-weight: 500;
  color: white;
}
